.section-title {
  margin: 0;
  margin-bottom: 48px;
  font-weight: 700;
  font-size: 32px;
  line-height: 100%;
  text-align: left;

  @include on-tablet {
    font-size: 40px;
    margin-bottom: 64px;
  }

  @include on-desktop {
    font-size: 48px;
  }

  @include animation-scroll(translateX(200px), translateX(0), 0.2s);
}
